import React from "react"
import { StaticImage } from "gatsby-plugin-image"

export interface BlogCardProps extends React.PropsWithChildren {
  title: string
  description: string
  href: string
  imgUrl?: string
}

const Card: React.FC<BlogCardProps> = ({
  title,
  description,
  imgUrl,
  href,
  children,
}) => {
  console.log(imgUrl)

  return (
    <div className={`project-card shadow`}>
      {children}
      <a href={`${href}`}>
        <div className={`card-tint`}></div>
      </a>

      <div className={`card-text`}>
        <div className={`title`}>{title}</div>
        <p>{description}</p>
      </div>
    </div>
  )
}

export default Card
