import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Card from "./Card"

interface QueryResponse {
  allMarkdownRemark: {
    nodes: {
      frontmatter: {
        title: string
        description?: string
        date?: string
        category?: string
        featuredImage?: {
          childImageSharp?: {
            fluid: {
              src: string
            }
          }
          publicURL: string
        }
      }
      fields: {
        slug: string
      }
    }[]
  }
}

const InactiveProjects: React.FC = () => {
  const query: QueryResponse = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        filter: { frontmatter: { projectStatus: { eq: "inactive" } } }
        sort: { frontmatter: { date: DESC } }
      ) {
        nodes {
          frontmatter {
            title
            description
            date
            category
            featuredImage {
              publicURL
            }
            projectStatus
          }
          fields {
            slug
          }
        }
      }
    }
  `)

  return (
    <div className={`project-grid`}>
      {query.allMarkdownRemark.nodes.map((project, index) => {
        const { title, description } = project.frontmatter
        const { slug } = project.fields
        const imgUrl = project.frontmatter?.featuredImage?.publicURL

        return (
          <Card
            key={`project_${index}`}
            title={title}
            description={description}
            href={slug}
            imgUrl={imgUrl}
          >
            <img
              src={imgUrl}
              alt="card background"
              className={`card-background`}
              placeholder="blurred"
            />
          </Card>
        )
      })}
    </div>
  )
}

export default InactiveProjects
